.store--card {
	display: flex;
	margin: 2em;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	color: aliceblue;
	max-width: 310px;
	border: aliceblue 2px solid;
	border-radius: 1em;
	overflow: hidden;
	transition: all 0.3s ease-in-out;
}
.store--card:hover {
	scale: 1.02;
}
.card--img {
	max-width: 310px;
}
.card--img img {
	width: 100%;
}
.card--text {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	padding-left: 2em;
	width: 320px;
}
.card--text h3,
.card--text p {
	margin: 0.4em 0;
	text-decoration: none;
}
a {
	text-decoration: none;
}
