.App {
	text-align: center;
}
body {
	background-color: black;
}
.App-logo {
	height: 40vmin;
	pointer-events: none;
}

.product--card {
	display: flex;
	margin: 2em;
	justify-content: center;
	align-items: center;
}
.product--card p {
	color: white;
	margin-top: 0.5em;
}
.product--card a,
.product--card span {
	color: #2997ff;
	text-decoration: none;
}
.product--link {
	margin-top: 1em;
}
.product--link a {
	margin-right: 0.2em;
	transition: all 0.3s ease-in-out;
}
.product--link a:hover {
	margin-right: 0.5em;
}
.product--card .product--title {
	font-family: '思源黑体';
	font-size: 56px;
	font-weight: 700;
	margin-top: 0;
}

.product--info {
	display: flex;
	flex-direction: column;
	height: 388px;
	justify-content: center;
	align-items: center;
	margin-left: 2em;
}
@media (max-width: 800px) {
	.product--card {
		flex-direction: column;
	}
	.product--info {
		margin-left: 0;
	}
}
@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-float infinite 3s ease-in-out;
	}
}

.App-header {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
}

.App-link {
	color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
	0% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(10px);
	}
	100% {
		transform: translateY(0px);
	}
}
.store {
	margin-top: 3em;
	display: flex;
	flex-direction: column;
	align-items: center;
}
body {
	overflow-x: hidden;
}
