.commodity--label,
.commodity--price {
	color: aliceblue;
}
.commodity--select {
	display: flex;
	margin-left: 3em;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.commodity--title {
	color: var(--title-color);
}
.commodity--img {
	max-width: 400px;
}
.commodity {
	margin-top: 3em;
	display: flex;
	/* flex-direction: column; */
	justify-content: center;
	align-items: flex-start;
}
.commodity--button {
	width: 10em;
	height: 4em;
	color: rgb(196, 196, 196);
	margin: 1em;
	background-color: transparent;
	border: #fff 1px solid;
	cursor: pointer;
	transition: all 0.3s ease-in-out;
}
.commodity--button:hover {
	background-color: var(--title-color);
	text-shadow: black 0px 0px 10px;
}
.commodity--buy,
.commodity--cart {
	width: 10em;
	height: 4em;
	color: rgb(255, 255, 255);
	font-weight: bold;
	text-shadow: black 0px 0px 10px;
	cursor: pointer;
	margin: 1em;
	margin-top: 3em;
	background-color: var(--title-color);
	transition: all 0.3s ease-in-out;
}
.commodity--buy:hover,
.commodity--cart:hover {
	/* color: rgb(196, 196, 196);
	 */
	translate: 0 -0.5em;
}
.commodity--button.active {
	border: var(--title-color) 3px solid;
	color: var(--title-color);
}
