:root {
	box-sizing: border-box;
}
*,
::after,
::before {
	box-sizing: inherit;
}
h2 {
	color: var(--title-color);
	text-align: center;
}
.cart--list {
	color: aliceblue;
	text-align: center;
}
li {
	color: #fff;
	list-style: none;
	/* vertical-align: middle; */
	display: flex;
	align-items: center;
}
ul {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 0;
}
input {
	display: none;
}
.cart--check {
	width: 1.2em;
	position: relative;
	display: inline-block;
	height: 1.2em;
	border: #fff 0.1em solid;
	border-radius: 100%;
	box-sizing: content-box;
	transition: all 0.2s ease-in-out;
	margin: 1em;
}
.cart--check::before {
	content: '';
	position: absolute;
	top: 0.3em;
	left: 0.3em;
	width: 0.6em;
	height: 0.6em;
	border-radius: 100%;
	background: #fff;
	opacity: 0;
	transition: all 0.3s ease;
}
label {
	display: flex;
}
input:checked + .cart--check {
	border: var(--title-color) 0.1em solid;
}
input:checked + .cart--check::before {
	opacity: 1;
}
.commodity--button {
	width: 10em;
	height: 4em;
	color: rgb(196, 196, 196);
	margin: 1em;
	background-color: transparent;
	border: #fff 1px solid;
	cursor: pointer;
	transition: all 0.3s ease-in-out;
}
.commodity--button:hover {
	background-color: var(--title-color);
	text-shadow: black 0px 0px 10px;
}
.commodity--buy,
.commodity--cart {
	width: 10em;
	height: 4em;
	color: rgb(255, 255, 255);
	font-weight: bold;
	text-shadow: black 0px 0px 10px;
	cursor: pointer;
	margin: 1em;
	margin-top: 3em;
	background-color: var(--title-color);
	transition: all 0.3s ease-in-out;
}
.commodity--buy:hover,
.commodity--cart:hover {
	/* color: rgb(196, 196, 196);
	 */
	translate: 0 -0.5em;
}
button[disabled] {
	opacity: 0.5;
	cursor: not-allowed;
}
